.actual-block {
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  //width: 375px;
  max-width: 768px;
  margin: 0 auto;
}

.actual-block__image-wrapper {
  display: block;
  width: 100%;
  height: 200px;
  background-color: $image-back1;
  //margin-right: auto;
  //font-size: 0;
  line-height: 0;
}

.actual-block__image {
  display: block;
  width: 100%;
  height: 100%;
  //max-width: 100%;
  object-fit: cover;
}

.actual-block__image-wrapper--padding {
  @include margin-reset;
  background-color: $image-back2;
  width: 57%;
  min-height: 275px;
  margin: 0 auto;
}

.actual-block__link-wrapper {
  width: 375px;
  margin: 0 auto;
}

.actual-block__link {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  display: inline-block;
  vertical-align: middle;
}

.actual-block__title {
  @include margin-reset;
  font-family: "Boita", "Times New Roman", serif;
  font-weight: 500;
  font-size: 30px;
  line-height: 30px;
  text-align: center;
  padding: 16px 30px 25px 30px;
}

.actual-block__button {
  display: none;
}

.actual-block--selection {
  margin: 0 auto;
  padding-top: 20px;
}

.actual-block--selection .actual-block__title {
  padding-bottom: 25px;
  margin-left: auto;
  margin-right: auto;
}

.actual-block--background {
  position: relative;
  border: none;
  min-height: 220px;
  padding-bottom: 0;
  justify-content: center;
  display: flex;
  margin: 0 auto;
  //width: 100%;
}

.actual-block--background:before {
  content: "";
  background-image: url("../img/promo-back-desk@1x.jpg");
  background-position: top center;
  background-repeat: no-repeat;
  width: 100%;
  height: 220px;
  position: absolute;
  top: 0;
  z-index: -1;
  background-size: cover;
  background-color: $image-back1;

  @media (min-resolution: $retina-dpi),
  (min-resolution: $retina-dppx) {
    background-image: url("../img/promo-back-desk@2x.jpg");
    background-size: cover;
  }
}

.actual-block--background .actual-block__link {
  padding: 15px 10px;
}

.actual-block--background .actual-block__title {
  text-align: center;
  margin: 0 auto;
  font-size: 31px;
  line-height: 35px;
  width: 365px;
}

@media (min-width: $landscape-width) {
  .actual-block--background {
    min-height: 270px;
  }

  .actual-block--background:before {
    height: 270px;
  }

  .actual-block--background .actual-block__title {
    text-align: center;
    margin: 0 auto;
    font-size: 36px;
    line-height: 40px;
    width: 465px;
  }

  .actual-block__image-wrapper {
    height: 300px;
  }
}

@media (min-width: $tablet-width) {
  .actual-block {
    max-width: 608px;
  }

  .actual-block__image-wrapper {
    width: 100%;
    margin: 0 auto;
    height: auto;
  }

  .actual-block--selection .actual-block__image-wrapper {
    width: 50%;
  }

  .actual-block__image {
    width: 100%;
  }

    .actual-block--background {
      min-height: 340px;
      width: 100%;
      max-width: none;
    }

    .actual-block--background:before {
      height: 340px;
    }

    .actual-block--background .actual-block__title {
      text-align: center;
      margin: 0 auto;
      font-size: 39px;
      line-height: 43px;
      width: 560px;
    }
}

@media (min-width: $laptop-width) {
